.burger {
  display: none;
}

.burger:hover {
  opacity: 0.7;
}

@media screen and (max-width: 889px) {
  .header {
    padding: 18px 30px;
  }

  .burger {
    position: relative;
    display: block;
    width: 44px;
    height: 39px;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }

  .burger-line {
    position: absolute;
    left: 50%;
    width: 27px;
    height: 2px;
    border-radius: inherit;
    background-color: #fff;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }

  .burger-line:first-child {
    top: 8px;
  }

  .burger-line:nth-child(3) {
    bottom: 8px;
  }

  .burger-line:nth-child(2) {
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  .header__wrapper {
    display: none;
  }

  .header__link_type_profile {
    display: none;
  }
}

@media screen and (max-width: 684px) {
  .header {
    padding: 18px 14px;
  }

  .header__link-register {
    margin-right: 14px;
    font-size: 10px;
    line-height: 160%;
  }

  .header__link-signin {
    padding: 7px 12px;
